@import url(https://fonts.googleapis.com/css?family=Roboto:400,500);


.Recipe * {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}



.Recipe {
	font-family: 'Roboto', sans-serif;
	font-weight: 400;
	font-size: 1rem;
	background-color: #f0ebe1;
	margin-bottom: 5px;
}

.Recipe .name {
	font-size: 1.4em;
	font-weight: 400;
	padding: 10px;
	cursor: pointer;
	border-bottom: 1px solid transparent;
	-webkit-transition: border-bottom .2s ease-out;
	-o-transition: border-bottom .2s ease-out;
	transition: border-bottom .2s ease-out;
	-webkit-transition-delay: .1s;
	     -o-transition-delay: .1s;
	        transition-delay: .1s;
}

.Recipe.active .name {
	border-bottom: 1px solid #d3d0ca;
}

.Recipe .ingredients {
	overflow: hidden;
	-webkit-transition: height .3s linear;
	-o-transition: height .3s linear;
	transition: height .3s linear;
}
.Recipe .ingredients .ingredients-inner {
	padding: 10px;
}


.Recipe input {
	display: block;
	width: 100%;
	margin-bottom: 5px;
	border-radius: 2px;
	border: 1px solid #d3d0ca;
	padding: 2px 5px;
}
.Recipe input:disabled {
	color: #333;
	border-color: transparent;
	padding-left: 0;
}


.ingredient__title {
	margin-top: 0;
	font-size: 1.2em;
	font-weight: 500;
}


